@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
* {
    font-family: "Lato", sans-serif;
    margin: 0;
    padding: 0;
}
body {
    padding: 30px;
}

input {
    width: 100%;
    height: 25px;
    border: 1px solid #efefef;
    width: 10px 0;
    border-radius: 5px;
}
.content{
display: -webkit-flex;
display: flex;
-webkit-flex-wrap: wrap;
        flex-wrap: wrap;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
margin-top: 10px;
}
.card {
    -webkit-align-content: center;
            align-content: center;
    border: 1px solid #efefef;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    width: 350px;
    mim-width: 350px;
    height: 220px;
    text-align: center;
}

.card img{
    width: 100%;
    height: 85%;
}
.card p{
    font-size: 14px;
}

